import React, { useState, useEffect } from "react";
import styled from "styled-components";
import InputBox from "../../components/InputBox/index";
import LongBtn from "../../components/Button/LongBtn";
import ProfileImg from "../../components/ImageBox";
import { useNavigate, useLocation } from "react-router-dom";
import userImg from "../../assets/images/user_img_big.svg";
import axios from "axios";

const FormContainer = styled.section`
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 90vh;
  width: 100vw;
  margin-top: 30px;
  font-family: "Spoqa Han Sans Neo", "sans-serif";
`;

const PageTitle = styled.h1``;

const ProfileEditTitle = styled.h2`
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  margin-bottom: 1.2rem;
  color: #000;
`;

const TitleExplain = styled.p`
  font-weight: 400;
  font-size: 14px;
  color: #767676;
`;

const ImgWrapper = styled.div`
  width: 110px;
  height: 110px;
  margin: 30px auto;
`;

const InputForm = styled.form`
  margin-bottom: 30px;
`;

const SetProfile = () => {
  const [username, setUsername] = useState("");
  const [accountname, setAccountname] = useState("");
  const [itemIntro, setItemIntro] = useState("");
  const [usernameError, setUsernameError] = useState("");
  const [accountnameError, setAccountnameError] = useState("");
  const [usernameValid, setUsernameValid] = useState(false);
  const [accountnameValid, setAccountnameValid] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();
  const email = location.state.email;
  const password = location.state.password;
  const passed = username && accountname;

  /*     */
  useEffect(() => {
    const usernameRegex = /^([-|-|a-z|A-Z|0-9|]){2,10}$/;

    if (!usernameRegex.test(username)) {
      setUsernameError("*    ");
      setUsernameValid(false);
    } else {
      setUsernameError("");
      setUsernameValid(true);
    }
  }, [username]);

  /* ID    */
  useEffect(() => {
    const accountnameRegex = /^([A-Za-z0-9_.])+$/;
    if (!accountnameRegex.test(accountname) && accountname !== "") {
      setAccountnameError("*, ,      .");
      setAccountnameValid(false);
    } else if (accountname === "") {
      setAccountnameValid(false);
    } else {
      setAccountnameError("");
      setAccountnameValid(true);
    }
  }, [accountname]);

  const signInHandler = async () => {
    try {
      const res = await axios.post(`${process.env.REACT_APP_API_KEY}/user`, {
        headers: {
          "Content-type": "application/json",
        },
        user: {
          username: username,
          accountname: accountname,
          intro: itemIntro,
          email: email,
          password: password,
          image: ProfileImg ? ProfileImg : userImg,
        },
      });
      navigate("/emaillogin");
    } catch (error) {
      if (error.response.data.message === "   ID.") {
        setAccountnameError("   ID.");
      }
    }
  };

  return (
    <FormContainer>
      <PageTitle className="ir"> </PageTitle>
      <ProfileEditTitle> </ProfileEditTitle>
      <TitleExplain>    .</TitleExplain>
      <ImgWrapper>
        <ProfileImg />
      </ImgWrapper>
      <InputForm>
        <InputBox
          id="username"
          labelText=" "
          placeholder="2~10  ."
          onChange={(e) => {
            setUsername(e.target.value);
          }}
        />
        <p className="message">{usernameError}</p>
        <InputBox
          id="accountname"
          labelText=" ID"
          placeholder=", , (.),(_)  ."
          onChange={(e) => {
            setAccountname(e.target.value);
          }}
        />
        <p className="message">{accountnameError}</p>
        <InputBox
          id="itemIntro"
          labelText=""
          placeholder="     !"
        />
      </InputForm>
      <LongBtn
        message="triplus "
        onClick={signInHandler}
        disabled={passed ? false : true}
      ></LongBtn>
    </FormContainer>
  );
};

export default SetProfile;
