import React, { useState, useEffect } from "react";
import styled from "styled-components";
import InputBox from "../../components/InputBox/index";
import LongBtn from "../../components/Button/LongBtn";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const FormContainer = styled.section`
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 80vh;
  width: 100vw;
  margin-top: 30px;
  font-family: "Spoqa Han Sans Neo", "sans-serif";
`;

const PageTitle = styled.h1``;

const SignUpTitle = styled.h2`
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #000000;
`;

const InputForm = styled.form`
  margin: 70px 0 30px;
`;

export default function EmailSignUp(props) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [emailValid, setEmailValid] = useState(false);
  const [passwordValid, setPasswordValid] = useState(false);
  const navigate = useNavigate();

  /*       */
  useEffect(() => {
    const emailRegex =
      /^([A-Z|a-z|0-9](\-|\_){0,1})+[A-Z|a-z|0-9]\@([A-Z|a-z|0-9])+((\.){0,1}[A-Z|a-z|0-9]){2}\.[a-z]{2,3}$/;

    if (!emailRegex.test(email) && email !== "") {
      setEmailError("*    .");
      setEmailValid(false);
    } else if (email === "") {
      setEmailError("");
      setEmailValid(false);
    } else {
      setEmailError("");
      setEmailValid(true);
    }
  }, [email]);

  /*        */
  useEffect(() => {
    if (password.length < 6 && password !== "") {
      setPasswordError("*  6  .");
      setPasswordValid(false);
    } else if (password === "") {
      setPasswordValid(false);
    } else {
      setPasswordError("");
      setPasswordValid(true);
    }
  }, [password]);

  /*     post */
  const sendData = async () => {
    const res = await axios.post(
      `${process.env.REACT_APP_API_KEY}/user/emailvalid`,
      {
        user: {
          email: email,
        },
      }
    );

    if (res.data.message === "   .") {
      setEmailValid(true);
      navigate("/setprofile", {
        state: {
          email: email,
          password: password,
        },
      });
    } else if (res.data.message === "    .") {
      setEmailError("*   .");
      setEmailValid(false);
    } else {
      setEmailValid(false);
      alert("   ");
    }
  };

  return (
    <FormContainer>
      <PageTitle className="ir">triplus  </PageTitle>
      <SignUpTitle> </SignUpTitle>
      <InputForm>
        <InputBox
          id="email"
          labelText=""
          placeholder="  "
          onChange={(e) => {
            setEmail(e.target.value);
          }}
        />
        <p className="message">{emailError}</p>
        <InputBox
          type="password"
          id="password"
          labelText=""
          placeholder=" "
          onChange={(e) => {
            setPassword(e.target.value);
          }}
        />
        <p className="message">{passwordError}</p>
      </InputForm>
      <LongBtn
        disabled={emailValid && passwordValid ? false : true}
        onClick={sendData}
        message=""
      ></LongBtn>
    </FormContainer>
  );
}
