import React from "react";
import styled, { css } from "styled-components";
import { Link } from "react-router-dom";
import spriteImg from "../../assets/images/css_sprites.png";

const SocialLoginItemList = styled.li`
  /* border   width    */
  box-sizing: border-box;
  width: 100%;
  background-color: transparent;
  border-radius: 44px;
  margin-bottom: 10px;

  /*  li margin-bottom 0px  */
  &:last-child {
    margin-bottom: 0px;
  }

  &::before {
    content: "";
    position: absolute;
    display: block;
    float: left;
    width: 24px;
    height: 24px;
    background-image: url(${spriteImg});
    background-repeat: no-repeat;
    /*       x, y  */
    background-position: ${(props) => props.x}px ${(props) => props.y}px;
    margin: 10px 0;
    margin-left: 14px;
  }

  ${(props) =>
    props.socialName === "" && //    &   
    //  css 
    css`
      border: 1px solid #f2c94c;
      &:hover {
        background-color: #fff1c6;
      }
    `}
  ${(props) =>
    props.socialName === "" && //    &   
    //  css 
    css`
      border: 1px solid #767676;
      &:hover {
        background-color: #cdcdcd;
      }
    `}
  
  ${(props) =>
    props.socialName === "" && //    &   
    //  css 
    css`
      border: 1px solid #2d9cdb;
      &:hover {
        background-color: #c9ebff;
      }
    `}
`;

const SocialLoginAnchor = styled(Link)`
  width: 100%;
  font-size: 14px;
  line-height: 17px;
  display: inline-block;
  padding: 13px 0;
  cursor: pointer;
`;

const SocialLoginItem = ({ socialName, x, y }) => {
  return (
    <SocialLoginItemList socialName={socialName} x={x} y={y}>
      <SocialLoginAnchor>{socialName}  </SocialLoginAnchor>
    </SocialLoginItemList>
  );
};

export default SocialLoginItem;
